//colors

$primary-color: #233468;

.originalBg {
  background-image: url("./assets/background.png");
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 100vh;
  cursor: pointer;
  @media (max-width: 768px) {
    background-position: center;
    zoom: 0.7;
  }
}

.main {
  position: relative;
  min-height: 100vh;
}
.bg {
  background-image: url("./assets/background.png");
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.1;
  background-repeat: repeat-y;
}
.get-started {
  cursor: pointer;
  min-height: 80vh;
}

.home {
  min-height: 90vh;
  animation: fadeIn ease 3s;

  .arrow-down {
    position: absolute;
    bottom: 25px;
    left: 50%;
    cursor: pointer;
    @media (max-width: 768px) {
      bottom: 35px;
      left: 35%;
    }
  }

  .home-badge {
    z-index: 3;
  }

  .content {
    display: flex;
    align-items: center;
    position: absolute;
    top: 20%;
    left: 20%;
    @media (max-width: 768px) {
      position: unset !important;
      top: unset;
      left: unset;
      padding: 20%;
      flex-direction: column;
      align-items: unset;
    }
  }

  .badge-content {
    display: flex;
    z-index: 4;
    margin-left: -26px;
    margin-top: 36%;
    @media (max-width: 768px) {
      margin-top: -30%;
      margin-left: unset;
      flex-direction: column;
      align-items: center;
    }
  }

  .badge-desc {
    font-size: 52px;
    @media (max-width: 768px) {
      font-size: 25px;
      margin-top: 1.5rem;
      margin-left: unset !important;
    }
    .dotContent {
      img {
        margin-left: 5px !important;
        width: 5px;
      }
    }
  }

  .select-badge {
    animation: fadeWidthOut ease 3s;
    position: absolute;
    left: 40%;
    top: 50%;
    z-index: 9999;
  }

  .badge-div {
    height: 160px;
    width: 55px;
    background: $primary-color;
    @media (max-width: 768px) {
      height: 50px;
      width: 150%;
    }
  }
}
/* animations */

@keyframes fadeIn {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeWidthOut {
  0% {
    width: 250px;
  }
  50% {
    width: 200px;
  }
  100% {
    opacity: 168px;
  }
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 100px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fadeInUpDot {
  from {
    transform: translate3d(0, 50px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

//global
.hand-cursor {
  cursor: pointer;
}

.red {
  color: red;
}

//sidebar
.sideBar {
  top: 0;
  font-family: "Gotham";
  position: absolute;
  background-color: #ffffff;
  z-index: 9999;
  width: 500px;
  @media (max-width: 768px) {
    width: 100%;
    padding: 20px;
  }
  height: 100vh;
  border-right: 10px solid $primary-color;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  ul {
    list-style: none;
    padding: unset;
  }
  a {
    color: black;
    &:hover {
      text-decoration: none;
      color: $primary-color;
    }
  }
  .titles {
    li {
      font-size: 25px;
      padding-bottom: 50px;
      @media (max-width: 768px) {
        padding-bottom: 20px;
      }
    }
  }
  .contacts {
    .icon {
      width: 25px;
      height: 25px;
    }
    .instagram {
      width: 24px;
      height: 18px;
    }
    .phone {
      width: 18px;
      height: 18px;
    }
    li {
      font-size: 20px;
    }
  }
  .closeBtn {
    width: 38px;
    height: 38px;
    cursor: pointer;
  }
}

//Header

.menu {
  width: 35px;
  height: 25px;
}

//Resident Zone
.residentZone {
  height: 85vh;
}
.fadeInUp {
  animation-duration: 1s;
  animation-fill-mode: both;
  -webkit-animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  opacity: 1;
  animation-name: fadeInUp;
  -webkit-animation-name: fadeInUp;
}

.fadeInUpDot {
  animation-duration: 2s;
  animation-fill-mode: both;
  -webkit-animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  opacity: 1;
  animation-name: fadeInUpDot;
  -webkit-animation-name: fadeInUpDot;
}

//selectors Container

.additional-height {
  height: unset !important;
  max-height: unset !important;
  overflow: unset !important;
}

.selectorsContainer {
  display: flex;
  // flex-wrap: wrap;
  width: 45%;
  position: absolute;
  top: 59%;
  left: 40.5%;
  z-index: 4;
  transition: 0.75s width ease-in-out;

  @media (max-width: 768px) {
    flex-direction: column;
    top: 30%;
    width: 90%;
    left: unset;
    margin-left: 5%;
    margin-right: 5%;
    transition: unset;
  }
  // transition: all 0.75s ease;
  .selectsContent {
    display: flex;
    flex-wrap: wrap;
    background-color: $primary-color;
    padding: 25px;
    height: 158px;
    max-height: 158px;
    overflow-y: hidden;
    overflow-x: hidden;
    .css-yk16xz-control {
      background-color: $primary-color;
      // color: white;
      border: unset;
    }
    .css-1okebmr-indicatorSeparator {
      display: none;
    }
    .css-1uccc91-singleValue {
      color: white;
    }
    .inputContainer {
      input {
        border: unset;
        background-color: #233468;
        height: 100%;
        border-bottom: 1px solid white;
        color: white;
        margin-left: 8px;
        outline: none;
        width: 100%;
      }
    }
  }
  .searchBtn {
    background: red;
    border: unset;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 768px) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
}

.selectorsContainer-small {
  background-color: $primary-color;
  width: 20px;
  .selectsContent {
    display: none;
  }
  .searchBtn {
    display: none;
  }
}

//Card Item
.card-container {
  .badge-content {
    .imgContent {
      img {
        height: 270px;
        object-fit: cover;
      }
    }
    .badgeContent {
      position: absolute;
      height: 35%;
      right: 0px;
      top: 25px;
      @media (max-width: 768px) {
        right: 0px;
        width: 40px;
        top: 10%;
      }
    }
  }
}

//category Detail
.cat-detail-content {
  .name {
    font-size: 3.5rem;
  }
  .descBtn {
    width: 300px;
    height: 78px;
    border: 1px solid lightgrey;
    background: transparent;
    text-align: center;
  }
}

//MapContent

.mapContainer {
  position: absolute;
  top: 0;
  left: 0;
}

.image-gallery-thumbnail .image-gallery-thumbnail-image {
  height: 80px;
}

//Property

